<template>
  <div class="container">
    <h1>Random Picker</h1>

    <!-- Input Section -->
    <div class="grid">
      <input v-model="newChoice" placeholder="Enter a choice" class="input" />
      <input
        type="number"
        v-model="newOdds"
        min="1"
        placeholder="Odds (default 1)"
        class="input"
      />
      <button @click="addChoice" class="contrast button thin-button">
        Add Choice
      </button>
    </div>

    <!-- Choices List -->
    <div v-if="choices.length > 0" class="choices">
      <li v-for="(choice, index) in choices" :key="index" class="choice-item">
        <span>{{ choice.name }} (Odds: {{ choice.odds }})</span>
        <button @click="removeChoice(index)" class="button small square-button">
          Remove
        </button>
      </li>
    </div>
    <p v-else>No choices added yet.</p>

    <!-- Spin Button -->
    <button
      @click="chooseWinner"
      :disabled="picking || choices.length === 0"
      class="button primary spin-button"
    >
      Choose Winner
    </button>

    <!-- Spinner Display -->
    <div class="spinner-container">
      <h2 class="spinner">{{ spinnerText }}</h2>

      <!-- Circular Lock-In Progress Bar -->
      <svg class="progress-circle" viewBox="0 0 36 36">
        <path
          class="progress-circle-bg"
          d="M18 2.0845
             a 15.9155 15.9155 0 0 1 0 31.831
             a 15.9155 15.9155 0 0 1 0 -31.831"
        />
        <path
          class="progress-circle-fill"
          :style="circleStyle"
          d="M18 2.0845
             a 15.9155 15.9155 0 0 1 0 31.831
             a 15.9155 15.9155 0 0 1 0 -31.831"
        />
      </svg>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // User inputs
      newChoice: "",
      newOdds: 1,

      // Default choices
      choices: [
        { name: "a", odds: 1 },
        { name: "b", odds: 2 },
      ],

      // Configuration constants
      MAX_LOCK_IN_TIME: 15000, // The maximum time for any lock-in phase (ms)
      MIN_FAKE_LOCK_DURATION: 4000, // Minimum fake lock duration in ms
      MAX_FAKE_LOCK_DURATION: 14000, // Maximum fake lock duration in ms
      MIN_SPIN_TIME: 0, // Minimum spin time in ms
      MAX_TOTAL_TIME: 120000, // Maximum total time in ms

      // State variables
      picking: false,
      confirmedWinner: false,
      spinnerText: "Waiting...",
      finalPick: null,
      remainingTime: 0, // Time remaining before final lock-in
      baitDuration: 0, // Fake duration for bait spins
      lockInProgress: 0, // Lock-in progress percentage
      isLockingIn: false, // Whether the system is in the real lock-in phase
      isFakeLocking: false, // Whether the system is in a fake lock-in phase
      previousFakeAnswer: null, // Previous fake answer to avoid repeats
    };
  },
  computed: {
    // Dynamically style the circular progress bar
    circleStyle() {
      const percentage = this.lockInProgress;

      // Start (blue) and end (green) colors in RGB
      const startColor = { r: 255, g: 140, b: 0 }; // #2196F3 255, 140, 0
      const endColor = { r: 76, g: 175, b: 80 }; // #4CAF50

      // Interpolation factor (percentage scaled between 0 and 1)
      const factor = percentage / 100;

      // Interpolating each color channel
      const r = Math.round(startColor.r + factor * (endColor.r - startColor.r));
      const g = Math.round(startColor.g + factor * (endColor.g - startColor.g));
      const b = Math.round(startColor.b + factor * (endColor.b - startColor.b));

      // Create the final interpolated color
      const color = `rgb(${r}, ${g}, ${b})`;

      return {
        strokeDasharray: `${percentage}, 100`,
        stroke: color,
      };
    },
  },
  methods: {
    addChoice() {
      if (this.newChoice.trim() && this.newOdds > 0) {
        this.choices.push({
          name: this.newChoice,
          odds: Number(this.newOdds),
        });
        this.newChoice = "";
        this.newOdds = 1;
      }
    },

    removeChoice(index) {
      this.choices.splice(index, 1);
    },

    // Main function to start picking
    async chooseWinner() {
      if (this.choices.length === 0 || this.picking) return;

      // Reset states
      this.picking = true;
      this.confirmedWinner = false;
      this.spinnerText = "Spinning...";
      this.previousFakeAnswer = null;
      this.finalPick = this.pickRandomChoice();
      this.baitDuration = this.getRandomBaitDuration();
      this.remainingTime = this.baitDuration;

      // Start the baiting phase
      await this.startBaiting();

      // Start the final lock-in phase
      await this.startFinalLockIn();
    },

    pickRandomChoice() {
      const weightedChoices = this.getWeightedChoices();
      const randomIndex = Math.floor(Math.random() * weightedChoices.length);
      return weightedChoices[randomIndex];
    },

    getWeightedChoices() {
      const weightedChoices = [];
      this.choices.forEach((choice) => {
        for (let i = 0; i < choice.odds; i++) {
          weightedChoices.push(choice.name);
        }
      });
      return weightedChoices;
    },

    async startBaiting() {
      this.isFakeLocking = true;

      // Continue baiting until time runs out
      while (this.remainingTime > 0) {
        const fakeAnswer = this.pickRandomFakeAnswer();
        const fakeLockDuration = this.getRandomFakeLockDuration();

        // Ensure the baiting phase ends on time
        const timeToRun = Math.min(fakeLockDuration, this.remainingTime);
        this.remainingTime -= timeToRun;

        // Display the fake answer and animate partial progress
        this.spinnerText = fakeAnswer;
        await this.animateProgress(timeToRun, this.MAX_LOCK_IN_TIME);

        // Immediately reset progress to 0 after completing one fake lock-in cycle
        this.lockInProgress = 0; // Smooth reset to 0
      }

      this.isFakeLocking = false;
      this.lockInProgress = 0;
    },

    pickRandomFakeAnswer() {
      let fakeAnswer;
      do {
        fakeAnswer = this.pickRandomChoice();
      } while (
        fakeAnswer === this.previousFakeAnswer &&
        this.choices.length > 1
      );

      this.previousFakeAnswer = fakeAnswer;
      return fakeAnswer;
    },

    async startFinalLockIn() {
      this.isLockingIn = true;
      this.spinnerText = this.finalPick;

      // Lock-in progress animation (full 5000 ms)
      await this.animateProgress(this.MAX_LOCK_IN_TIME, this.MAX_LOCK_IN_TIME);

      this.finalizeWinner();
    },

    finalizeWinner() {
      this.spinnerText = this.finalPick;
      this.confirmedWinner = true;
      this.picking = false;
      this.isLockingIn = false;
    },

    animateProgress(duration, maxDuration) {
      return new Promise((resolve) => {
        const startTime = performance.now();
        const updateProgress = (time) => {
          const elapsedTime = time - startTime;
          this.lockInProgress = (elapsedTime / maxDuration) * 100;

          if (elapsedTime < duration) {
            requestAnimationFrame(updateProgress);
          } else {
            this.lockInProgress = (duration / maxDuration) * 100;
            resolve();
          }
        };

        requestAnimationFrame(updateProgress);
      });
    },

    getRandomBaitDuration() {
      return (
        Math.random() * (this.MAX_TOTAL_TIME - this.MIN_SPIN_TIME) +
        this.MIN_SPIN_TIME
      );
    },

    getRandomFakeLockDuration() {
      return (
        Math.random() *
          (this.MAX_FAKE_LOCK_DURATION - this.MIN_FAKE_LOCK_DURATION) +
        this.MIN_FAKE_LOCK_DURATION
      );
    },
  },
};
</script>

